import Vue from 'vue'
import Router from 'vue-router'


const index  = () => import('../views/index');
const home  = () => import('../views/home');
const modifyPage  = () => import('../views/modifyPage');

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: index,
      redirect: '/index',
      children:[
        {path: '/index', name: 'home', component: home},
        {path: '/modifyPage', name: 'modifyPage', component: modifyPage},
      ]
    }
  ]
})



